// Create custom element wrapper for Wix
class SophosicChatElement extends HTMLElement {
  static get observedAttributes() {
    return ['api-url', 'primary-color', 'secondary-color'];
  }

  constructor() {
    super();
    if (!this.shadowRoot) {
      this.attachShadow({ mode: 'open' });
      // Create container immediately
      const container = document.createElement('div');
      container.id = 'sophosic-chat-container-' + Math.random().toString(36).substr(2, 9);
      this.containerId = container.id;
      this.shadowRoot.appendChild(container);
    }
  }

  connectedCallback() {
    // Add any configuration options passed from Wix
    window.CHAT_CONFIG = {
      apiUrl: this.getAttribute('api-url') || 'https://n8n.sophosic.ai/webhook/e60704a9-52c1-47eb-9037-2344d6b56c21/chat',
      theme: {
        primary: this.getAttribute('primary-color') || '#5423E7',
        secondary: this.getAttribute('secondary-color') || '#1E1B3C'
      }
    };

    // Initialize the chat widget
    this.initializeChat();
  }

  initializeChat() {
    if (window.SophosicChat && this.containerId) {
      window.SophosicChat.mount('#' + this.containerId);
    } else {
      // Retry after a short delay if SophosicChat isn't loaded yet
      setTimeout(() => this.initializeChat(), 100);
    }
  }
}

// Use IIFE to avoid global scope pollution
(() => {
  const elementName = 'sophosic-chat-widget';
  if (!customElements.get(elementName)) {
    customElements.define(elementName, SophosicChatElement);
  }
})(); 